import React from "react";
import Link from "gatsby-link";
import Helmet from "react-helmet";
import LazyLoad from 'react-lazyload';
import FontAwesomeIcon from '@fortawesome/react-fontawesome';
import faAngleRight from '@fortawesome/fontawesome-free-solid/faAngleRight';
import faAngleLeft from '@fortawesome/fontawesome-free-solid/faAngleLeft';
import faWheelchair from '@fortawesome/fontawesome-free-solid/faWheelchair';
import faCheckCircle from '@fortawesome/fontawesome-free-solid/faCheckCircle';

export default function Cardio({ data }) {
  const { edges: posts } = data.allMarkdownRemark;
  return (
    <div className="preview-feed">
    <div className="page-top-banner">
      <h2 className="page-heading" style={cardioStyle}> Кардио упражнения</h2>
      <Link to="/" className="page-btn-back"><FontAwesomeIcon icon={faAngleLeft} className="preview-btn-icon" /> Все упражнения</Link>
    </div>
      {posts
        .filter(post => post.node.frontmatter.filterby === 'cardio')
        .map(({ node: post }) => {
          return (
            <LazyLoad height={200}>
            <Helmet title={`Группа мышц - ${post.frontmatter.bodypart}`} />
            <div className="preview" key={post.id} style={{ backgroundImage: `url(${post.frontmatter.image})` }}>
              <div className="preview-wrapper">
                <div className="preview-containers">
                  <div className="preview-container-1">
                      <Link to={post.frontmatter.path} className="preview-title">
                        {post.frontmatter.title}
                      </Link>
                    <div>
                    <span className="preview-stats">
                      <Link to={`/bodypart/${post.frontmatter.filterby}`} className="tag tag-bodypart">
                          {post.frontmatter.bodypart}
                      </Link>
                      <Link to={`/type/${post.frontmatter.filtertype}`} className="tag tag-type">
                        {post.frontmatter.type}
                      </Link>
                      <Link to={`/type/${post.frontmatter.filtertype}`} className="tag tag-inventory">
                        {post.frontmatter.inventory}
                      </Link>
                    </span>
                    <div>
                      <Link to={`/stats/efficiency`} className="tag tag-efficiency">
                        <FontAwesomeIcon icon={faCheckCircle} /> {post.frontmatter.efficiency}/10
                      </Link>
                      <Link to={`/stats/injury`} className="tag tag-injury">
                        <FontAwesomeIcon icon={faWheelchair} /> {post.frontmatter.injury}/10
                      </Link>
                    </div>
                    </div>
                    <p className="preview-excerpt">{post.excerpt}</p>
                  </div>
                  <div className="preview-container-2">
                    <Link to={post.frontmatter.path}>
                      <img src={post.frontmatter.image} className="preview-image" />
                    </Link>
                  </div>
                </div>
              </div>
              <Link to={post.frontmatter.path} className="preview-btn">
                Подробнее <FontAwesomeIcon icon={faAngleRight} className="preview-btn-icon" />
              </Link>
            </div>
            </LazyLoad>
          );
        })}
    </div>
  );
}

const cardioStyle = {
	backgroundImage: 'url(https://s17.postimg.cc/v74zjlwpb/cardio.jpg)',
	backgroundPosition: "bottom"
};

export const pageQuery = graphql`
  query CardioQuery {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          excerpt(pruneLength: 180)
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
            image
            image_2
            image_3
            image_4
            filterby
            efficiency
            injury
            bodypart
            filtertype
            type
            filterinventory
            inventory
            filterplace
          }
        }
      }
    }
  }
`;



